.day{
        background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.418) ), 
        url("images/morning-pc.jpg");
        background-size: cover;
        background-position: center;
}
@media only screen and (max-width: 600px) {
    .day{
        background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.418) ), 
        url('images/morning-mob.jpg');
        background-size: cover;
        background-position: center;
    }
}
.night{
        background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.418) ), 
        url("images/nightpc.jpg");
        background-size: cover;
        background-position: center;
}
@media only screen and (max-width: 600px) {
    .night{
        background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.418) ), 
        url('images/night-mob.png');
        background-size: cover;
        background-position: center;
    }
}

.main-container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.search {
    outline: none;
    padding: 20px 7%;
    border-radius: 20px;
    border: none;
    margin-bottom:4%;
    background: rgba(250, 250, 250, 0.85);

}
@media only screen and (max-width: 600px){
    .search{
        background: rgba(250, 250, 250, 0.2);
        
    }
}

.city {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 40px 8%;
    border-radius: 20px;
    background: rgba(250, 250, 250, 0.85);
    box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
}
@media only screen and (max-width: 600px){
    .city{
        background:transparent;   
    }
}

p {
    margin-top: 10px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.city-temp {
    font-size: 5rem;
    font-weight: bold;
    margin-top: 10px;
    color: #1e2432;
    text-align: center;
}
.more{
    font-size: 1em;
    color: #1e2432;
     margin-top: 12px;   
}
@media only screen and (max-width: 600px){
    .more span{
        color: grey;
    }
}
.city sup {
    font-size: 0.5em;
}

.city-name {
    font-size: 2em;
}
.state-code {
    font-size: 2em;
}
.city-name sup {
    padding: 0.2em 0.6em;
    margin-left: 0.2em;
    border-radius: 30px;
    color: #fff;
    background: #ff8c00;
}

.city-icon {
    margin-top: 10px;
    width: 100px;
    height: 100px;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
}
@media only screen and (max-width: 600px){
    .info p{
        color:grey;
    }
}

@media only screen and (max-width: 600px) {
    .search {
        padding: 20px 15%;
    }

    .city {
        padding: 40px 20%;
    }
}